import React, { useState } from 'react';
import "../styles/Auth.css";
import { Spinner, Alert } from "react-bootstrap";
import { registerUser, loginUser, requestPasswordReset } from '../api'; // Import the submitDailyReport function
import { useNavigate } from 'react-router-dom';




const Auth = (props) => {
  const [isLogin, setIsLogin] = useState(true);
  const [loading, setLoading] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');


  //  const to alert
  const [alertVisible, setAlertVisible] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [alertType, setAlertType] = useState("info");

  // const to reset password
  const [isReset, setIsReset] = useState(false);

  // const para redirigir a una url 
  const navigate = useNavigate();


  const showAlert = (message, type = "info") => {
    setAlertMessage(message);
    setAlertType(type); // Set the alert type
    setAlertVisible(true);
    setTimeout(() => {
      setAlertVisible(false);
    }, 5000);
  };

  const toggleAuthMode = () => {
    if (isReset) {
      setIsReset(false);
    } else {
      setIsLogin(!isLogin);
    }
  };

  const redirectToCheckout = () => {
    window.open('https://buy.stripe.com/test_8wM4j797v0RP4mc145', '_blank');
  };

  const redirectToAuth = () => {
    navigate("/auth");;
  };

  const handleResetRequest = async (event) => {
    event.preventDefault();
    setLoading(true);
    const email = event.target.email.value;
    try {
      const data = await requestPasswordReset(email);
      setLoading(false);
      showAlert(data.message, "success");
    } catch (error) {
      console.error('Failed to request password reset:', error);
      showAlert("An error occurred, please try again", "danger");
      setLoading(false);
    }
  };

  const handleLogin = async (email, password) => {
    try {
      const data = await loginUser(email, password);
      // Aquí puedes manejar la respuesta de la API después del inicio de sesión
      // Guarda el token en el almacenamiento local
      localStorage.setItem('token', data.token);
      localStorage.setItem("username", data.username);
      setLoading(false);
      props.updateAuth(true);
      navigate('/');
    } catch (error) {
      console.error('Failed to login:', error);
      // Si hay un error en el inicio de sesión, muestra el mensaje de error
      if (error.message) {
        showAlert(error.message, "danger");
        setLoading(false);
      } else {
        showAlert('Unknown bug. Please try again.', "danger");
        setLoading(false);
      }
    }
  };

  const handleRegister = async(username, email, password, phone) => {
    // Guardar los datos del formulario de registro en el almacenamiento local
    // localStorage.setItem("register_email", email);
    // localStorage.setItem("register_password", password);
    // localStorage.setItem("register_username", username);
    // localStorage.setItem("register_phone", phone);
  
    // // Redirigir al enlace de pago de Stripe
    // redirectToCheckout();
    
    try {
      const data = await registerUser(username, email, password, phone);
      
      // Redirigir al enlace de pago de Stripe
      window.location.href = data.sessionUrl;
    } catch (error) {
      console.error('Error:', error);
    }

  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);
    setSuccessMessage('');
    const email = event.target.email.value;
    const password = event.target.password.value;

    if (isLogin) {
      // try {
      //   // const data = await loginUser(email, password);
        
      //   // // Aquí puedes manejar la respuesta de la API después del inicio de sesión

      //   // // Guarda el token en el almacenamiento local
      //   // localStorage.setItem('token', data.token);
      //   // localStorage.setItem("username", data.username);
      //   // setLoading(false);
      //   // props.updateAuth(true);
      //   // navigate('/');

      // } catch (error) {

      //   console.error('Failed to login:', error);
      //   // Si hay un error en el inicio de sesión, muestra el mensaje de error
      //   if (error.message) {
      //     showAlert(error.message, "danger");
      //     setLoading(false);
      //   } else {
      //     showAlert('Unknown bug. Please try again.', "danger");
      //     setLoading(false);
      //   }
      // }

      handleLogin(email, password);

    } else {
      const username = event.target.username.value;
      const phone = event.target.phone.value;
      
      // para usar stripe 
      // handleRegister(username, email, password, phone);
      event.target.reset();
      try {
        const data = await registerUser(username, email, password, phone);
        setLoading(false);
        // Aquí puedes manejar la respuesta de la API después del registro
        console.log("despues de data")
        if (data.error) {
          setLoading(false);
          console.log("error in data register user")
          showAlert("Error in create user", "danger");

        } else {
          // Guarda el token en el almacenamiento local
          // localStorage.setItem('token', data.token);
          setLoading(false);
          showAlert('Sign up successful, please log in');
          
        }
        event.target.reset();
      } catch (error) {
        console.error('Failed to register:', error);
        setLoading(false);
        showAlert(error.message, "danger");

      }
    }
  };



  
  
  return (
    <div className="auth-container">
      <div className="auth-card">
      
        {/* <h2>{isLogin ? 'Log in' : 'Sign up'}</h2> */}
        <h2>{isReset ? 'Reset Password' : (isLogin ? 'Log in' : 'Sign up')}</h2>
        <hr></hr>
        {alertVisible && (
          <Alert variant={alertType} onClose={() => setAlertVisible(false)} dismissible>
            {alertMessage}
          </Alert>
        )}
        {/* <form className="auth-form" onSubmit={handleSubmit}> */}
        <form className="auth-form" onSubmit={isReset ? handleResetRequest : handleSubmit} >
          {/* {!isLogin && ( */}
          {!isReset && !isLogin && (
            <>  <input
              type="text"
              name="username"
              placeholder="Username"
              required />
              <input
                type="text"
                name="phone"
                placeholder="phone"
                required />
            </>
          )}
      

          {isReset ? (
            <input
              type="email"
              name="email"
              placeholder="Email"
              required
            />
          ) : (
            <>
              <input
                type="email"
                name="email"
                placeholder="Email"
                required
              />
              <input
                type="password"
                name="password"
                placeholder="Password"
                required
              />
            </>
          )}
          <button className="button-shared  auth-submit" type="submit">
            {isReset ? 'Request Reset' : (isLogin ? 'Log in' : 'Sign up')}
            {loading && (
              <Spinner
                animation="border"
                size="sm"
                style={{ marginLeft: '8px' }}
              />
            )}
          </button>
    

        </form>
              

           <button className="button-shared  auth-toggle " onClick={toggleAuthMode}>
        {isReset
          ? 'Back to Login'
          : (isLogin
              ? 'You do not have an account? Sign up'
              : 'Do you already have an account? Log in')}
      </button>

      {!isReset && (
        <button
          className="button-shared  auth-toggle "
          onClick={() => setIsReset(true)}
        >
          Forgot Password?
        </button>
      )}
      <br></br>
      {/* <button className="button-shared auth-submit" onClick={redirectToCheckout}>
       Pay
      </button> */}

      </div>
    </div>
  );
};


export default Auth;



